@import '../../fonts';

$bgColor: #EFEFEF;
$color: #333;
$primary-color: #58A8D5;

.FlippableCard {
  perspective: 1000px;
  display: block;
  width: 300px;
  height: auto; }

.card-container {
  position: relative;
  display: block;
  margin: 20px;
  border-radius: 5px;
  width: 100%;
  height: 100%;
  background: #EFEFEF;
  box-shadow: 0 3px 20px 3px rgba(0, 0, 0, 0.3);
  transform-style: preserve-3d;
  transform: none;
  transition: all .5s ease;
  .card-front, .card-back {
    backface-visibility: hidden;
    transform: rotateX(0deg);
    .card-header {
      margin: 0;
      padding: 5px;
      padding-top: 10px;
      padding-bottom: 10px;
      border-radius: 5px 5px 0 0;
      color: $bgColor;
      font-size: 1.5rem;
      text-align: center;
      cursor: pointer; }
    .card-body {
      margin: 0;
      padding: 10px;
      text-align: justify;
      p {
        margin: 0;
        margin-bottom: 10px; }
      .btn-group {
        display: flex;
        justify-content: flex-end;
        align-items: flex-start;
        .btn {
          padding: 5px;
          border-radius: 5px;
          text-decoration: none;
          background: $bgColor;
          color: $color;
          transition: all .5s ease; }
        .btn:hover {
          background: $color;
          color: $bgColor;
          transition: all .3s ease; } } } }
  .card-front {
    display: block; }
  .card-back {
    transform: rotateY(180deg);
    display: none; } }

.card-container:hover {
  box-shadow: 0 8px 20px 5px rgba(0, 0, 0, 0.5);
  transition: all .3s ease; }

.card-container.flipped {
  transition: all .3s ease;
  transform: rotateY(180deg) translateZ(-50px);
  .card-front {
    display: none; }
  .card-back {
    display: block; } }
