body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App{font-family:'Raleway', serif;position:absolute;width:100%;height:100%;box-sizing:border-box}

*{box-sizing:border-box}#layout-container{width:100%;height:100%;overflow-y:scroll;scroll-behavior:smooth;scroll-snap-type:y mandatory;background-image:url(data:image/png;base64,);background-repeat:repeat;background-position:top;background-size:contain}.dark-bg{background-image:url(/static/media/dark-honeycomb.b591ad61.png);background-repeat:repeat;background-position:top;background-size:auto}.section{display:flex;flex-direction:column;align-items:center;justify-content:center;text-align:center;width:100%;height:100vh;padding:0;scroll-snap-align:center}.section h1{font-family:'Libre Baskerville', sans-serif}.section h1 .header-icons{position:relative;display:inline-block;color:darkgray}.section#footer{height:auto}.navbar{position:fixed;left:0;z-index:1;width:60px;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.navbar ul.bubbles{list-style:none;height:auto;justify-content:center;padding:0}.navbar ul.bubbles li{font-size:1rem;margin:1em;padding:0;border-radius:2.5em;overflow:wrap}.navbar ul.bubbles li a{height:2.5em;width:2.5em;margin:0;padding:0;line-height:2.5em;padding-left:20px;padding-right:20px;text-decoration:none;text-transform:uppercase;display:inline-block;color:transparent;border-radius:inherit;overflow:hidden;background:rgba(0,0,0,0.4);transition:color .5s ease, background .5s ease}.navbar ul.bubbles li a:hover{color:white;background:rgba(0,0,0,0.8);transition:color .3s ease, background .3s ease}

.Introduction{background-image:url(/static/media/background.54b78615.jpg);background-color:#333;background-repeat:no-repeat;background-position:top;background-size:cover;box-sizing:border-box;display:table;width:100%;height:100%;text-align:center;padding:10px;color:#EFEFEF;text-shadow:0 2px 10px #121212}.Introduction a{color:inherit}.Introduction .hoverable{text-shadow:0 2px 10px #121212;transition:text-shadow .5s ease}.Introduction .hoverable:hover{text-shadow:0 2px 30px black;transition:text-shadow .3s ease}.Introduction .clickable{cursor:pointer;-webkit-user-select:none;user-select:none;transition:color .5s ease, text-shadow .5s ease}.Introduction .clickable:hover{color:#58A8D5;text-shadow:0 0 30px #58A8D5;transition:color .3s ease, text-shadow .3s ease}.Introduction .Introduction-container{display:table-cell;vertical-align:middle}.Introduction .Introduction-container .info{padding:0 30px;font-size:1em}.Introduction .Introduction-container .info .long-name{display:inline-block}@media screen and (max-width: 520px){.Introduction .Introduction-container .info .long-name{display:none}}.Introduction .Introduction-container .info .short-name{display:none}@media screen and (max-width: 520px){.Introduction .Introduction-container .info .short-name{display:inline-block}}.Introduction .Introduction-container .info .intro-rows{margin:20px 50px}.Introduction .Introduction-container .info .name,.Introduction .Introduction-container .info .last-name{font-family:'Libre Baskerville', serif}.Introduction .Introduction-container .info .name{font-size:4em}.Introduction .Introduction-container .info .name .last-name{color:#58A8D5}.Introduction .Introduction-container .info .desc{font-size:1em;color:#bcbcbc;margin-bottom:50px}.Introduction .Introduction-container .info .links{font-size:1.8em}.Introduction .Introduction-container .info .links a{margin-left:10px;margin-right:10px}

.Tag{display:inline-block;margin:5px;margin-left:5px;margin-right:5px;padding-left:15px;padding-right:15px;font-size:0.8rem;background:#4d996e;color:#EFEFEF;border-radius:30px;height:30px}.Tag .tag-name{margin:0;line-height:30px}

.Portfolio{width:100%;height:100%;padding:40px;overflow-y:auto;box-sizing:border-box}.Portfolio h1{margin-bottom:30px;font-size:2rem}.Portfolio #project-list{list-style:none;padding:0;display:flex;align-items:flex-start;justify-content:space-evenly;flex-wrap:wrap}

.FlippableCard{-webkit-perspective:1000px;perspective:1000px;display:block;width:300px;height:auto}.card-container{position:relative;display:block;margin:20px;border-radius:5px;width:100%;height:100%;background:#EFEFEF;box-shadow:0 3px 20px 3px rgba(0,0,0,0.3);-webkit-transform-style:preserve-3d;transform-style:preserve-3d;-webkit-transform:none;transform:none;transition:all .5s ease}.card-container .card-front,.card-container .card-back{-webkit-backface-visibility:hidden;backface-visibility:hidden;-webkit-transform:rotateX(0deg);transform:rotateX(0deg)}.card-container .card-front .card-header,.card-container .card-back .card-header{margin:0;padding:5px;padding-top:10px;padding-bottom:10px;border-radius:5px 5px 0 0;color:#EFEFEF;font-size:1.5rem;text-align:center;cursor:pointer}.card-container .card-front .card-body,.card-container .card-back .card-body{margin:0;padding:10px;text-align:justify}.card-container .card-front .card-body p,.card-container .card-back .card-body p{margin:0;margin-bottom:10px}.card-container .card-front .card-body .btn-group,.card-container .card-back .card-body .btn-group{display:flex;justify-content:flex-end;align-items:flex-start}.card-container .card-front .card-body .btn-group .btn,.card-container .card-back .card-body .btn-group .btn{padding:5px;border-radius:5px;text-decoration:none;background:#EFEFEF;color:#333;transition:all .5s ease}.card-container .card-front .card-body .btn-group .btn:hover,.card-container .card-back .card-body .btn-group .btn:hover{background:#333;color:#EFEFEF;transition:all .3s ease}.card-container .card-front{display:block}.card-container .card-back{-webkit-transform:rotateY(180deg);transform:rotateY(180deg);display:none}.card-container:hover{box-shadow:0 8px 20px 5px rgba(0,0,0,0.5);transition:all .3s ease}.card-container.flipped{transition:all .3s ease;-webkit-transform:rotateY(180deg) translateZ(-50px);transform:rotateY(180deg) translateZ(-50px)}.card-container.flipped .card-front{display:none}.card-container.flipped .card-back{display:block}

.Skills{width:100%;height:100%;padding:40px;box-sizing:border-box}.Skills #skills-content{background:transparent;overflow:auto}.Skills h1{margin-bottom:30px;font-size:2rem}.Skills .Tag{font-size:1.2rem}

.Academic{width:90%;height:100%;padding:40px;box-sizing:border-box}.Academic .Card{margin:20px}.Academic h1{margin-bottom:30px;font-size:2rem}.Academic .historic{display:flex;flex-direction:column;align-items:stretch;justify-content:space-evenly;flex-wrap:nowrap}.Academic .qualification,.Academic .institution,.Academic .location{font-family:sans;display:block;margin:8px;text-align:left}.Academic .qualification{font-weight:600;font-size:1.1rem}.Academic .location{font-size:0.85rem;color:gray;font-style:italic}

.Card{display:inline-block;position:relative;width:auto;height:auto;padding:0;background:#EFEFEF;box-shadow:0 3px 10px 2px rgba(0,0,0,0.2);border-radius:5px;transition:all .5s ease}.Card .header{margin:0;padding:5px;padding-left:10px;padding-right:10px;font-size:1.5rem;color:#EFEFEF;text-align:center;border-radius:5px 5px 0 0}.Card .content{padding:10px;border-radius:0 0 5px 5px}.Card:hover{box-shadow:0 8px 15px 5px rgba(0,0,0,0.5);transition:all .3s ease}

.Footer{display:flex;flex-direction:row;align-items:flex-start;justify-content:space-between;box-sizing:border-box;width:100%;padding:20px;color:#EFEFEF;text-shadow:0 2px 10px #121212}.Footer .footer-text{width:60%;display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:left}.Footer .footer-text a{color:cyan}.Footer .contact-links{display:flex;flex-direction:column;justify-content:center;align-items:flex-start;padding:10px;width:40%}@media screen and (max-width: 840px){.Footer .contact-links{align-items:center}}.Footer .contact-links .contact-link{text-decoration:none;color:#EFEFEF}

.IconizedLink{box-sizing:border-box;text-shadow:none;margin:10px}.IconizedLink a>span{color:#EFEFEF;background:#3a708d}.IconizedLink a:hover .icon{transition:background-color .3s ease, color .3s ease, -webkit-transform .3s ease;transition:transform .3s ease, background-color .3s ease, color .3s ease;transition:transform .3s ease, background-color .3s ease, color .3s ease, -webkit-transform .3s ease;-webkit-transform:scale(1.2);transform:scale(1.2)}.icon,.link{display:inline-block;font-size:16px;line-height:16px;vertical-align:middle}.icon{padding:12px;border-radius:50%;font-size:24px;transition:background-color .5s ease, color .5s ease, -webkit-transform .5s ease;transition:transform .5s ease, background-color .5s ease, color .5s ease;transition:transform .5s ease, background-color .5s ease, color .5s ease, -webkit-transform .5s ease}.link{padding:8px;padding-left:15px;border-radius:0 10px 10px 0;margin-left:-10px}@media screen and (max-width: 840px){.link{display:none}}

