@import '../../fonts';

$bgColor: #EFEFEF;
$color: #333;
$primary-color: #58A8D5;

.Card {
  display: inline-block;
  position: relative;
  width: auto;
  height: auto;
  padding: 0;
  background: $bgColor;
  box-shadow: 0 3px 10px 2px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  transition: all .5s ease;
  .header {
    margin: 0;
    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;
    font-size: 1.5rem;
    color: $bgColor;
    text-align: center;
    border-radius: 5px 5px 0 0; }
  .content {
    padding: 10px;
    border-radius: 0 0 5px 5px; } }

.Card:hover {
  box-shadow: 0 8px 15px 5px rgba(0, 0, 0, 0.5);
  transition: all .3s ease; }
